import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { 
  Brain, 
  Globe, 
  FlaskConical, 
  History, 
  Music, 
  Trophy, 
  Cpu, 
  Leaf,
  ArrowLeft,
  Play
} from 'lucide-react';

const Categories = () => {
  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [selectedCategory, setSelectedCategory] = useState(null);

  useEffect(() => {
    // Get username from session storage
    const storedUsername = sessionStorage.getItem('guestUsername');
    if (!storedUsername) {
      // If no username, redirect back to home
      navigate('/');
      return;
    }
    setUsername(storedUsername);
  }, [navigate]);

  const categories = [
    {
      id: 'general',
      name: 'General Knowledge',
      description: 'Test your knowledge across various topics',
      icon: Brain,
      color: 'from-blue-500 to-cyan-500',
      hoverColor: 'from-blue-600 to-cyan-600',
      factCount: 50
    },
    {
      id: 'science',
      name: 'Science & Technology',
      description: 'Discover the wonders of science and tech',
      icon: FlaskConical,
      color: 'from-purple-500 to-pink-500',
      hoverColor: 'from-purple-600 to-pink-600',
      factCount: 45
    },
    {
      id: 'history',
      name: 'History & Culture',
      description: 'Journey through time and civilizations',
      icon: History,
      color: 'from-yellow-500 to-orange-500',
      hoverColor: 'from-yellow-600 to-orange-600',
      factCount: 40
    },
    {
      id: 'geography',
      name: 'Geography & World',
      description: 'Explore the world and its wonders',
      icon: Globe,
      color: 'from-green-500 to-emerald-500',
      hoverColor: 'from-green-600 to-emerald-600',
      factCount: 35
    },
    {
      id: 'entertainment',
      name: 'Entertainment & Arts',
      description: 'Movies, music, and creative expressions',
      icon: Music,
      color: 'from-pink-500 to-rose-500',
      hoverColor: 'from-pink-600 to-rose-600',
      factCount: 30
    },
    {
      id: 'sports',
      name: 'Sports & Athletics',
      description: 'Test your sports knowledge',
      icon: Trophy,
      color: 'from-red-500 to-pink-500',
      hoverColor: 'from-red-600 to-pink-600',
      factCount: 25
    },
    {
      id: 'technology',
      name: 'Modern Technology',
      description: 'Latest tech trends and innovations',
      icon: Cpu,
      color: 'from-indigo-500 to-purple-500',
      hoverColor: 'from-indigo-600 to-purple-600',
      factCount: 30
    },
    {
      id: 'nature',
      name: 'Nature & Environment',
      description: 'Explore the natural world',
      icon: Leaf,
      color: 'from-teal-500 to-green-500',
      hoverColor: 'from-teal-600 to-green-600',
      factCount: 35
    }
  ];

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
    
    // Scroll down to the Start Game section after a short delay
    setTimeout(() => {
      const startGameSection = document.querySelector('[data-start-game]');
      if (startGameSection) {
        startGameSection.scrollIntoView({ 
          behavior: 'smooth', 
          block: 'center' 
        });
      }
    }, 100);
  };

  const handleStartGame = () => {
    if (selectedCategory) {
      // Store selected category in session storage
      sessionStorage.setItem('selectedCategory', selectedCategory.id);
      sessionStorage.setItem('selectedCategoryName', selectedCategory.name);
      
      // Navigate to solo game
      navigate('/solo-game');
    }
  };

  const handleBackToHome = () => {
    // Clear session data and go back to home
    sessionStorage.removeItem('guestUsername');
    sessionStorage.removeItem('selectedCategory');
    sessionStorage.removeItem('selectedCategoryName');
    navigate('/');
  };

  if (!username) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-purple-900 flex items-center justify-center">
        <div className="text-white text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto mb-4"></div>
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-purple-900">
      {/* Header */}
      <div className="bg-white/10 backdrop-blur-sm border-b border-white/20">
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <button
                onClick={handleBackToHome}
                className="flex items-center space-x-2 text-white/70 hover:text-white transition-colors"
              >
                <ArrowLeft className="w-5 h-5" />
                <span>Back to Home</span>
              </button>
            </div>
            
            <div className="text-center">
              <h1 className="text-2xl font-bold text-white">Choose Your Category</h1>
              <p className="text-white/70">Welcome, {username}!</p>
            </div>
            
            <div className="flex items-center space-x-2 text-white/70">
              <Trophy className="w-5 h-5" />
              <span>Guest Mode</span>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 py-8">
        <motion.div 
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-center mb-12"
        >
          <h2 className="text-4xl font-bold text-white mb-4">Select a Category</h2>
          <p className="text-xl text-white/80 max-w-2xl mx-auto">
            Choose from our carefully curated categories. Each category contains a mix of real facts and AI-generated fake facts for you to identify.
          </p>
        </motion.div>

        {/* Categories Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
          {categories.map((category, index) => (
            <motion.div
              key={category.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              onClick={() => handleCategorySelect(category)}
              className={`bg-white/10 backdrop-blur-sm rounded-2xl p-6 cursor-pointer transition-all duration-300 border-2 ${
                selectedCategory?.id === category.id
                  ? 'border-blue-400 bg-white/20 scale-105'
                  : 'border-white/20 hover:border-white/40 hover:bg-white/15'
              }`}
            >
              <div className={`w-16 h-16 bg-gradient-to-r ${category.color} rounded-xl flex items-center justify-center mx-auto mb-4`}>
                <category.icon className="w-8 h-8 text-white" />
              </div>
              
              <h3 className="text-xl font-bold text-white mb-2">{category.name}</h3>
              <p className="text-white/70 text-sm mb-4">{category.description}</p>
              
              <div className="flex items-center justify-between text-white/60 text-sm">
                <span>{category.factCount} facts</span>
                {selectedCategory?.id === category.id && (
                  <div className="w-2 h-2 bg-blue-400 rounded-full animate-pulse"></div>
                )}
              </div>
            </motion.div>
          ))}
        </div>

        {/* Start Game Button */}
        {selectedCategory && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center"
            data-start-game
          >
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-8 max-w-md mx-auto border border-white/20">
              <h3 className="text-2xl font-bold text-white mb-4">
                Ready to Play {selectedCategory.name}?
              </h3>
              <p className="text-white/70 mb-6">
                You'll be presented with {selectedCategory.factCount} facts. Identify which ones are real and which are AI-generated!
              </p>
              
              <button
                onClick={handleStartGame}
                className="w-full bg-gradient-to-r from-green-500 to-emerald-500 text-white py-4 px-8 rounded-lg font-semibold text-lg hover:from-green-600 hover:to-emerald-600 transition-all duration-200 flex items-center justify-center space-x-2"
              >
                <Play className="w-5 h-5" />
                <span>Start Game</span>
              </button>
            </div>
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default Categories; 