import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { 
  ArrowLeft, 
  CheckCircle, 
  XCircle, 
  Trophy,
  User,
  Brain
} from 'lucide-react';

const SoloGame = () => {
  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedCategoryName, setSelectedCategoryName] = useState('');
  const [currentFact, setCurrentFact] = useState(0);
  const [score, setScore] = useState(0);
  const [gameStarted, setGameStarted] = useState(false);

  useEffect(() => {
    // Get data from session storage
    const storedUsername = sessionStorage.getItem('guestUsername');
    const storedCategory = sessionStorage.getItem('selectedCategory');
    const storedCategoryName = sessionStorage.getItem('selectedCategoryName');
    
    if (!storedUsername || !storedCategory) {
      // If missing data, redirect back to home
      navigate('/');
      return;
    }
    
    setUsername(storedUsername);
    setSelectedCategory(storedCategory);
    setSelectedCategoryName(storedCategoryName);
  }, [navigate]);

  const handleBackToCategories = () => {
    navigate('/categories');
  };

  const handleStartGame = () => {
    setGameStarted(true);
  };

  const handleAnswer = (isReal) => {
    // For now, just increment score and fact counter
    setScore(score + 1);
    setCurrentFact(currentFact + 1);
    
    // Simulate game progression
    if (currentFact >= 9) { // 10 facts total
      // Game completed
      alert(`Game completed! Your score: ${score + 1}/10`);
      navigate('/categories');
    }
  };

  if (!username || !selectedCategory) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-purple-900 flex items-center justify-center">
        <div className="text-white text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto mb-4"></div>
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-purple-900">
      {/* Header */}
      <div className="bg-white/10 backdrop-blur-sm border-b border-white/20">
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <button
                onClick={handleBackToCategories}
                className="flex items-center space-x-2 text-white/70 hover:text-white transition-colors"
              >
                <ArrowLeft className="w-5 h-5" />
                <span>Back to Categories</span>
              </button>
            </div>
            
            <div className="text-center">
              <h1 className="text-2xl font-bold text-white">{selectedCategoryName}</h1>
              <div className="flex items-center justify-center space-x-4 text-white/70 text-sm">
                <div className="flex items-center space-x-1">
                  <User className="w-4 h-4" />
                  <span>{username}</span>
                </div>
                <div className="flex items-center space-x-1">
                  <Trophy className="w-4 h-4" />
                  <span>Score: {score}</span>
                </div>
                <div className="flex items-center space-x-1">
                  <Brain className="w-4 h-4" />
                  <span>Fact {currentFact + 1}/10</span>
                </div>
              </div>
            </div>
            
            <div className="flex items-center space-x-2 text-white/70">
              <Trophy className="w-5 h-5" />
              <span>Guest Mode</span>
            </div>
          </div>
        </div>
      </div>

      {/* Main Game Content */}
      <div className="max-w-4xl mx-auto px-4 py-8">
        {!gameStarted ? (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center"
          >
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-8 max-w-2xl mx-auto border border-white/20">
              <h2 className="text-3xl font-bold text-white mb-6">Ready to Play?</h2>
              <p className="text-xl text-white/80 mb-8">
                Welcome, <span className="text-yellow-300 font-semibold">{username}</span>! 
                You're about to test your knowledge in <span className="text-blue-300 font-semibold">{selectedCategoryName}</span>.
              </p>
              
              <div className="bg-white/5 rounded-xl p-6 mb-8">
                <h3 className="text-xl font-bold text-white mb-4">How to Play:</h3>
                <ul className="text-white/80 space-y-2 text-left max-w-md mx-auto">
                  <li className="flex items-center space-x-2">
                    <span className="w-2 h-2 bg-blue-400 rounded-full"></span>
                    <span>You'll see 10 facts one by one</span>
                  </li>
                  <li className="flex items-center space-x-2">
                    <span className="w-2 h-2 bg-blue-400 rounded-full"></span>
                    <span>Decide if each fact is REAL or FAKE</span>
                  </li>
                  <li className="flex items-center space-x-2">
                    <span className="w-2 h-2 bg-blue-400 rounded-full"></span>
                    <span>Get points for correct answers</span>
                  </li>
                  <li className="flex items-center space-x-2">
                    <span className="w-2 h-2 bg-blue-400 rounded-full"></span>
                    <span>See your final score at the end</span>
                  </li>
                </ul>
              </div>
              
              <button
                onClick={handleStartGame}
                className="bg-gradient-to-r from-green-500 to-emerald-500 text-white py-4 px-8 rounded-lg font-semibold text-lg hover:from-green-600 hover:to-emerald-600 transition-all duration-200"
              >
                Start Game
              </button>
            </div>
          </motion.div>
        ) : (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center"
          >
            <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-8 max-w-2xl mx-auto border border-white/20">
              <div className="mb-6">
                <h2 className="text-2xl font-bold text-white mb-4">Fact #{currentFact + 1}</h2>
                <div className="bg-white/5 rounded-xl p-6 mb-6">
                  <p className="text-lg text-white/90 leading-relaxed">
                    {currentFact === 0 && "The Great Wall of China is visible from space with the naked eye."}
                    {currentFact === 1 && "A day on Venus is longer than a year on Venus."}
                    {currentFact === 2 && "Bananas are berries, but strawberries are not."}
                    {currentFact === 3 && "The human body contains enough iron to make a 3-inch nail."}
                    {currentFact === 4 && "Honey never spoils. Archaeologists have found pots of honey in ancient Egyptian tombs that are over 3,000 years old and still perfectly edible."}
                    {currentFact === 5 && "The shortest war in history lasted only 38 minutes between Britain and Zanzibar in 1896."}
                    {currentFact === 6 && "A group of flamingos is called a 'flamboyance'."}
                    {currentFact === 7 && "The average person spends 6 months of their lifetime waiting for red lights to turn green."}
                    {currentFact === 8 && "Octopuses have three hearts and blue blood."}
                    {currentFact === 9 && "The first oranges weren't orange - they were green."}
                  </p>
                </div>
              </div>
              
              <div className="flex gap-4 justify-center">
                <button
                  onClick={() => handleAnswer(true)}
                  className="flex items-center space-x-2 bg-gradient-to-r from-green-500 to-emerald-500 text-white py-4 px-8 rounded-lg font-semibold hover:from-green-600 hover:to-emerald-600 transition-all duration-200"
                >
                  <CheckCircle className="w-5 h-5" />
                  <span>REAL</span>
                </button>
                
                <button
                  onClick={() => handleAnswer(false)}
                  className="flex items-center space-x-2 bg-gradient-to-r from-red-500 to-pink-500 text-white py-4 px-8 rounded-lg font-semibold hover:from-red-600 hover:to-pink-600 transition-all duration-200"
                >
                  <XCircle className="w-5 h-5" />
                  <span>FAKE</span>
                </button>
              </div>
            </div>
          </motion.div>
        )}
      </div>
    </div>
  );
};

export default SoloGame; 